.footer {
  height: 200px;
  background-color: black;
  width: 100%;
  position: absolute;
  bottom: 0;
  display: flex;
  flex-direction: column;
  justify-content: space-around;
  align-items: center;
  z-index: 6;

  h1 {
    text-align: center;
    color: white;
  }
}

.links {
  color: white;
  width: 100%;
  min-height: 100px;
  position: relative;
}

.linkWrap {
  display: flex;
  align-items: center;
  justify-content: center;
}


